import React,{useState, useEffect} from 'react';
import { WhyAccordian } from '../data/data';

const Accordian  = () => {

    const [clicked, setClicked] = useState();


const toggle = index => {

  if(clicked === index){
    return setClicked(null)
  }

  setClicked(index);

}


return(
    <>
   
    {
  WhyAccordian.map((accor, index) => {
    return(
      <li key={index}>
              <a onClick={() => toggle(index)} data-bs-toggle="collapse" className="collapse" data-bs-target={`#accordion-list-${index + 1}`} >
                  <span>{accor.Id}</span> {accor.Ques} <i className="bx bx-chevron-down icon-show"></i><i className="bx bx-chevron-up icon-close"></i>
               
               </a>
              <div id={`accordion-list-${index +1 }`} className="collapse show"  data-bs-parent=".accordion-list">
                {clicked === index ?<p>{accor.Ans}</p> : null }                
               
                                
              </div>
            </li>
    )


  })

}

</>
      
)

}

export default Accordian;