import React from "react";
import { Clients } from "../data/data";
import './Client.css';


const Client = () => {
return(
    <section id="clients" className="clients section-bg">
      <div className="container">

        <div className="row" data-aos="zoom-in">
              {
                  Clients.map((cli, index) => {
                       return(
                        <div key={index} className="col-lg-2 col-md-4 col-6 d-flex align-items-center justify-content-center" key={index +1}>
                                <img src={cli.images}  alt={`image${index + 1 }`}/>
                         </div> 
                       ) 

                  })

              }
          </div>
      </div>
    </section>

)

}

export default Client;