import React from 'react';
import './Services.css';
import {ServicesCard} from '../data/data';
import 'boxicons';



const Services = () => {

    return(
        <section id="services" className="services section-bg">
        <div className="container" data-aos="fade-up">
          <div className="section-title">
            <h2>Services</h2>
            <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p>
          </div>
  
          <div className="row">



{
    ServicesCard.map((services, index) => {
return(
<div key={index} className="col-xl-3 col-md-6 d-flex align-items-stretch mt-4 mt-md-0" data-aos="zoom-in" data-aos-delay="200">
              <div className="icon-box">
                <div className="icon"><i className="bx bx-file"></i></div>
                <h4><a href="">{services.heading}</a></h4>
                <p>{services.text}</p>
              </div>
            </div>
)

    })

}
    

          </div>
       </div>
      </section>

    )

}


export default Services;