import React from 'react';
import './Team.css';
import {Teammate} from '../data/data'

const Team = () =>{
    return(
        <section id="team" className="team section-bg">
      <div className="container" data-aos="fade-up">

        <div className="section-title">
          <h2>Team</h2>
          <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p>
        </div>

        <div className="row">

{
    Teammate.map((elem, index) => {
        const {heading, leader, description, image} = elem;
        return(
            <div className="col-lg-6 mb-4" key={index}>
            <div className="member d-flex align-items-start" data-aos="zoom-in" data-aos-delay="100">
              <div className="pic"><img src={image} className="img-fluid" alt="" /></div>
              <div className="member-info">
                <h4>{heading}</h4>
                <span>{leader}</span>
                <p>{description}</p>
                <div className="social">
                  <a href=""><i className="ri-twitter-fill"></i></a>
                  <a href=""><i className="ri-facebook-fill"></i></a>
                  <a href=""><i className="ri-instagram-fill"></i></a>
                  <a href=""> <i className="ri-linkedin-box-fill"></i> </a>
                </div>
              </div>
            </div>
          </div>
        )


    })
}

          

        </div>

      </div>
    </section>

    )

}



export default Team;