import React, {useState} from 'react';
import './Portfolio.css'
import {PortfolioData} from '../data/data';


const Portfolio = () => {

    const [items, setItems] = useState(PortfolioData);

    const filterItem = (catgItem) => {
        const updatedItems = PortfolioData.filter((curElem) => {
            return curElem.category === catgItem;
        });
        setItems(updatedItems);


    }

    return(
        <section id="portfolio" className="portfolio">


        <div className="container" data-aos="fade-up">
  
          <div className="section-title">
            <h2>Portfolio</h2>
            <p>Magnam dolores commodi suscipit. Necessitatibus eius consequatur ex aliquid fuga eum quidem. Sit sint consectetur velit. Quisquam quos quisquam cupiditate. Et nemo qui impedit suscipit alias ea. Quia fugiat sit in iste officiis commodi quidem hic quas.</p>
          </div>
  
          <ul id="portfolio-flters" className="d-flex justify-content-center" data-aos="fade-up" data-aos-delay="100">
            <li  className="filter-active" onClick={()=> setItems(PortfolioData)}>All</li>
            <li  onClick={()=> filterItem('filterApp')}>App</li>
            <li  onClick={()=> filterItem('filterCard')}>Card</li>
            <li  onClick={()=> filterItem('filterWeb')}>Web</li>
          </ul>
  
          <div className="row portfolio-container" data-aos="fade-up" data-aos-delay="200">

{
    items.map((data, index) => {
        const {id, image,heading,text,dataLink,dataGallery,category} = data;
        return(
            <div className="col-lg-4 col-md-6 portfolio-item" key={index + 1} >
            <div className="portfolio-img">
                <img src={image} className="img-fluid" alt={heading} />
             </div>

            <div className="portfolio-info">
              <h4>{heading}</h4>
              <p>{text}</p>
              <a href={dataLink} data-gallery={dataGallery} className="portfolio-lightbox preview-link" title="App 1"><i className="bx bx-plus"></i></a>
              <a href="portfolio-details.html" className="details-link" title="More Details"><i className="bx bx-link"></i></a>
            </div>
          </div>

        )

    })

}
          </div>
  
        </div>
      </section>


    )

}

export default Portfolio;
